<template>
  <v-card elevation="0" :disabled="loading" :loading="loading">
    <BlockItem>
      <template v-slot:icon>
        mdi-text-box-edit-outline
      </template>
      <template v-slot:title>
        متن درباره ما
      </template>

      <v-form ref="form" >
        <v-row class="px-3" >
          <v-col cols="12" md="12" >
            <ckeditor :editor="editor" v-model="content" :config="editorConfig"></ckeditor>
          </v-col>
          <v-col class="py-0" cols="12" md="12" >
            <v-btn class="dash-btns dash-btns--blue d-block mx-auto" :loading="loading" dark height="40" @click="updateF()" >ثبت تغییرات</v-btn>
          </v-col>
        </v-row>
      </v-form>

    </BlockItem>
    <template v-slot:progress>
      <GDLoading/>
    </template>
  </v-card>
</template>

<script>
import BlockItem from "@/components/Misc/BlockItem";
import GDLoading from "@/components/Dashboard/Misc/GDLoading";
import CKEditor from '@ckeditor/ckeditor5-vue2';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import '@ckeditor/ckeditor5-build-classic/build/translations/fa';

Vue.use(CKEditor);
import axios from "axios";
import Vue from "vue";

export default {
  name: "AboutUsDash",
  components: {GDLoading, BlockItem},
  created() {
    this.fetchAboutUs()
  },
  data () {
    return {
      loading : false,
      editor: ClassicEditor,
      editorConfig: {
        language: 'fa',
      },

      content : null,
    }
  },
  methods : {
    updateF(){
        this.loading = true;
        axios.patch(this.$store.state.host + '/website/api/v1/website/change-about',{
          content : this.content
        },{
          headers : {
            Authorization: `Bearer ${this.$store.state.userId}`,
          }
        }).then(()=>{
          this.notify('تغییرات ثبت شد.','info');
        }).catch(error=>{
          this.errorHandle(error.response)
        }).finally(()=>{
          this.loading = false;
        })
    },
    fetchAboutUs () {
      this.loading = true;
      axios.get(this.$store.state.host + '/website/api/v1/website/about-us').then(response=>{
        this.content = response.data.data.content;
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.loading = false;
      })
    }

  }
}
</script>

<style lang="scss">
.ck * {
  font-family: "Shabnam FD" !important;
}

.ck-editor__editable {
  min-height: 170px;
}

.ck.ck-editor__editable.ck-focused:not(.ck-editor__nested-editable) {
  border: 2px solid #1e53a1;
}

.error-ck .ck.ck-editor__editable {
  border: 2px solid #dc3545 !important;
}
</style>